body {
  color: #333;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  color: #fff;
  z-index: 1000;
  background-color: #333;
  padding: 20px 0;
  position: sticky;
  top: 0;
}

.header__title {
  text-align: center;
  margin: 0;
  font-size: 24px;
}

.header__nav {
  justify-content: center;
  margin-top: 10px;
  display: flex;
}

.header__nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav ul li {
  margin: 0 15px;
  display: flex;
}

.header__nav ul li a {
  color: #fff;
  font-size: 18px;
  text-decoration: none;
  transition: color .3s;
}

.header__nav ul li a:hover {
  color: #e94e77;
}

.hero {
  color: #fff;
  text-align: center;
  background: url("https://www.sliderrevolution.com/wp-content/uploads/2024/01/clothing-website-design.jpg") center / cover no-repeat;
  padding: 100px 0;
}

.hero__title {
  margin: 0 0 20px;
  font-size: 36px;
}

.hero__subtitle {
  margin: 0 0 30px;
  font-size: 22px;
}

.hero__cta {
  color: #fff;
  background-color: #e94e77;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.hero__cta:hover {
  background-color: #d73a5e;
}

.content {
  background-color: #fff;
  padding: 60px 0;
}

.content__title {
  text-align: center;
  margin: 0 0 20px;
  font-size: 28px;
}

.content__text {
  text-align: center;
  margin: 0 0 20px;
  font-size: 16px;
  line-height: 1.6;
}

.footer {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 20px 0;
}

.footer__text {
  margin: 0;
  font-size: 14px;
}
